@media (max-width: 600px) {
  .people-grid {
    grid-template-columns: 1fr;
  }
}
.person-card {
  background: var(--card);
  border: 1px solid var(--border-subtle);
  border-radius: 1rem;
  overflow: hidden;
  transition: all 280ms cubic-bezier(0.19, 1, 0.22, 1);
  cursor: pointer;
  position: relative;
  text-decoration: none;
  display: block;
}
.person-card:hover,
.person-card:focus {
  border-color: var(--border);
  box-shadow: var(--shadow-lg);
  transform: translateY(-6px);
}
.person-card:focus {
  box-shadow: var(--shadow-lg), 0 0 0 3px var(--brand-accent-soft);
  outline: none;
}
.person-card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, var(--brand-accent-soft), transparent 50%);
  opacity: 0;
  transition: opacity 450ms cubic-bezier(0.19, 1, 0.22, 1);
  pointer-events: none;
  z-index: 1;
}
.person-card:hover::before {
  opacity: 1;
}
.person-card .card-actions {
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  display: flex;
  gap: 0.25rem;
  opacity: 0;
  transform: translateY(-8px);
  transition: all 120ms cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 10;
}
.person-card:hover .card-actions,
.person-card:focus .card-actions {
  opacity: 1;
  transform: translateY(0);
}
.person-avatar-section {
  position: relative;
  padding: 2rem 1.5rem 1.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.person-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--muted), var(--border));
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  position: relative;
  overflow: hidden;
  transition: all 280ms cubic-bezier(0.19, 1, 0.22, 1);
}
.person-card:hover .person-avatar {
  transform: scale(1.05);
}
.person-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.person-avatar .initials {
  font-family: var(--font-serif);
  font-size: 1.75rem;
  font-weight: 500;
  color: var(--muted-foreground);
  letter-spacing: 0.05em;
}
.person-type-badge {
  position: absolute;
  bottom: -4px;
  right: -4px;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-sm);
  border: 2px solid var(--card);
  transition: transform 120ms cubic-bezier(0.19, 1, 0.22, 1);
}
.person-card:hover .person-type-badge {
  transform: scale(1.1);
}
.person-type-badge.executive {
  background: var(--brand-accent);
  color: white;
}
.person-type-badge.board_member {
  background: var(--brand-accent-gold);
  color: white;
}
.person-type-badge.founder {
  background: #8b5cf6;
  color: white;
}
.person-type-badge.investor {
  background: #10b981;
  color: white;
}
.person-type-badge.owner {
  background: #6366f1;
  color: white;
}
.person-name {
  font-family: var(--font-display);
  font-size: 1.375rem;
  font-weight: 500;
  color: var(--foreground);
  margin-bottom: 0.25rem;
  line-height: 1.3;
  transition: color 120ms cubic-bezier(0.19, 1, 0.22, 1);
}
.person-card:hover .person-name {
  color: var(--brand-accent);
}
.person-title {
  font-family: var(--font-serif);
  font-size: 1rem;
  font-style: italic;
  color: var(--muted-foreground);
  margin-bottom: 0.75rem;
}
.person-location {
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  color: var(--stone);
}
.person-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border-top: 1px solid var(--border-subtle);
  background: var(--background-secondary);
}
.person-stat {
  padding: 1rem;
  text-align: center;
  border-right: 1px solid var(--border-subtle);
  transition: background 120ms cubic-bezier(0.19, 1, 0.22, 1);
}
.person-stat:last-child {
  border-right: none;
}
.person-card:hover .person-stat {
  background: var(--muted);
}
.person-stat-value {
  font-family: var(--font-display);
  font-size: 1.375rem;
  font-weight: 500;
  color: var(--foreground);
  line-height: 1;
  margin-bottom: 0.25rem;
}
.person-stat-label {
  font-size: 0.6875rem;
  font-weight: 500;
  color: var(--stone);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.person-companies {
  padding: 1rem 1.25rem;
  border-top: 1px solid var(--border-subtle);
}
.person-company-tag {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.25rem 0.75rem;
  background: var(--muted);
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 500;
  color: var(--secondary-foreground);
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  transition: all 120ms cubic-bezier(0.19, 1, 0.22, 1);
}
.person-company-tag:hover {
  background: var(--brand-accent-soft);
  color: var(--brand-accent);
  transform: translateY(-1px);
}
.person-company-tag.ceo-role {
  background: var(--brand-accent-soft);
  color: var(--brand-accent);
}
.person-company-tag .role-indicator {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: currentColor;
}
.person-card.list-item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 1.25rem;
  padding: 1rem 1.25rem;
  border-radius: 0.75rem;
}
.person-card.list-item:hover {
  transform: translateX(4px);
}
.person-card.list-item .person-avatar-section {
  padding: 0;
}
.person-card.list-item .person-avatar {
  width: 56px;
  height: 56px;
  margin-bottom: 0;
}
.person-card.list-item .person-avatar .initials {
  font-size: 1.125rem;
}
.person-card.list-item .person-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.person-card.list-item .person-name {
  font-size: 1rem;
  margin-bottom: 0;
}
.person-card.list-item .person-title {
  font-size: 0.875rem;
  margin-bottom: 0;
}
.person-card.list-item .person-stats {
  display: flex;
  gap: 1rem;
  border: none;
  background: none;
}
.person-card.list-item .person-stat {
  padding: 0;
  border: none;
  text-align: left;
}
.person-card.list-item .person-stat-value {
  font-size: 1rem;
}
.stats-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 1rem;
}
@media (max-width: 900px) {
  .stats-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 600px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.stat-card {
  background: var(--card);
  border: 1px solid var(--border-subtle);
  border-radius: 1rem;
  padding: 1.25rem;
  text-align: center;
  transition: all 280ms cubic-bezier(0.19, 1, 0.22, 1);
  cursor: default;
}
.stat-card:hover {
  border-color: var(--border);
  box-shadow: var(--shadow-md);
  transform: translateY(-3px);
}
.stat-icon {
  width: 44px;
  height: 44px;
  margin: 0 auto 0.75rem;
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.stat-icon.total {
  background: var(--brand-accent-soft);
  color: var(--brand-accent);
}
.stat-icon.ceo {
  background: rgba(59, 130, 246, 0.08);
  color: #3b82f6;
}
.stat-icon.board {
  background: var(--brand-accent-gold-soft);
  color: var(--brand-accent-gold);
}
.stat-icon.investor {
  background: rgba(34, 197, 94, 0.08);
  color: #22c55e;
}
.stat-icon.company {
  background: rgba(139, 92, 246, 0.08);
  color: #8b5cf6;
}
.stat-value {
  font-family: var(--font-display);
  font-size: 1.75rem;
  font-weight: 400;
  color: var(--foreground);
  line-height: 1.1;
  margin-bottom: 0.25rem;
}
.stat-label {
  font-size: 0.75rem;
  font-weight: 600;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--muted-foreground);
}
.filter-tabs {
  display: flex;
  gap: 0.5rem;
  background: var(--muted);
  padding: 0.25rem;
  border-radius: 0.75rem;
  overflow-x: auto;
}
.filter-tab {
  padding: 0.75rem 1.25rem;
  background: transparent;
  border: none;
  border-radius: 0.5rem;
  font-family: inherit;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--muted-foreground);
  cursor: pointer;
  transition: all 120ms cubic-bezier(0.19, 1, 0.22, 1);
  white-space: nowrap;
}
.filter-tab:hover {
  color: var(--foreground);
}
.filter-tab.active {
  background: var(--card);
  color: var(--foreground);
  box-shadow: var(--shadow-sm);
}
.filter-tab .count {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 20px;
  height: 20px;
  padding: 0 6px;
  margin-left: 0.5rem;
  background: var(--muted);
  border-radius: 9999px;
  font-size: 0.6875rem;
  font-weight: 700;
  color: var(--muted-foreground);
}
.filter-tab.active .count {
  background: var(--brand-accent-soft);
  color: var(--brand-accent);
}
.view-toggle {
  display: flex;
  background: var(--muted);
  border-radius: 0.5rem;
  padding: 0.25rem;
}
.view-btn {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  border-radius: 0.25rem;
  color: var(--muted-foreground);
  cursor: pointer;
  transition: all 120ms cubic-bezier(0.19, 1, 0.22, 1);
}
.view-btn:hover {
  color: var(--foreground);
}
.view-btn.active {
  background: var(--card);
  color: var(--foreground);
  box-shadow: var(--shadow-sm);
}
